body {
    margin: 0;
    padding: 0;
  }
  
  /* Background settings for the animated section of the website  */
  .sky {
    overflow-x: hidden;
    position: relative;
    background-image: linear-gradient( #80bfd8, #f3b55f, #35722c, #35adf3);
    width: 100%;
    height: 250vh;
    display: flex;
    justify-content: center;
  }
  
  /* Default settings for images in animated section */
  .sky img {
    position: absolute;
    bottom: 0;
    z-index: 2;
  }
  
  /* Container for "About Me" section */
  .info {
    width: 50%;
    margin: 164vh 25vh 52vh;
    border: solid black 2px;
    border-radius: 30px;
    background-color:whitesmoke;
    box-shadow: 0px 10px 15px rgb(0, 0, 0, 0.7);
    display:inline-table;
    z-index: 2;
  }
  
  /* Head shot image in "About me" section */
  .hs {
    width: 38vh;
    /* box-shadow: 0px 0px -10px -10px whitesmoke; */
    border-radius: 15px;
    margin-bottom: 0;
    left: 39%;
    border: solid black 2px;
    box-shadow: 0px 10px 15px rgb(0, 0, 0, 0.7);
  }
  
  /* Headings in "About Me" and "Projects" sections */
  .subtitles {
    text-align: center;
    font-size: 3.5vh;
    font-weight:900;
    margin-bottom: 1vh;
    text-shadow: 1px 1px 1px black;
    font-family: 'BioRhyme';
  }
  
  /* Text in "About Me" and "Projects" sections */
  .text {
    font-size: 2.5vh;
    font-family:Arial, Helvetica, sans-serif;
    margin-left: 3vw;
    margin-right: 3vw;
    margin-top: 0vh;
    min-width: 45%;
    text-align: center;
  }
  
  /* Background Setting for "Projects" section. No animation */
  .projects {
    height: 110vh;
    background-image: linear-gradient(#35adf3,#f8f8f8);
    overflow-x: hidden;
    position: relative;
    display: flex;
    justify-content: center;
  }
  
  /* Default setting for images in "Projects" section */
  .projects img {
    position: absolute;
    width: 100%;
    /* bottom: 0; */
    /* z-index: 2; */
  }
  
  /* Background Image at bottom of site. No animation. */
  .beach{
    height: 110vh;
    filter:drop-shadow(3px 3px 5px black);
  }
  
  /* Card for displaying each project */
  .tech {
    width: 90%;
    margin: 38vh 2vh 4vh;
    border: solid black 2px;
    border-radius: 20px;
    background-color:whitesmoke;
    box-shadow: 0px 10px 15px rgb(0, 0, 0, 0.7);
    display:table-column;
    z-index: 2;
  }
  
  /* Project link */
  .tech a {
    font-size: 4vh;
    color: black;
  }
  
  .tech a:hover {
    filter:drop-shadow(0px 1px 3px black);
  }
  
