  /* Scroll Down Arrow */
  .section {
    position: relative;
    z-index: 2;
    left: 35%;
  }
  .section::before {
    animation: bounce 1s ease infinite;
    bottom: 162vh;
    color: #fff;
    content: '╲╱';
    font-size: 4.5vh;
    font-weight:950;
    height: 4rem;
    letter-spacing: -1px;
    line-height: 4rem;
    filter:drop-shadow(5px 5px 6px black);
    position: absolute;
    text-align: center;
    width: 6rem;
  }
  @keyframes bounce {
    50% {
      transform: translateY(-50%);
    }
  }
  
  